.modalBackground {
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  background-color: var(--light-gray);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalContainer {
  width: 65vw;
  height: 80vh;
  border-radius: 12px;
  background-color: var(--body-color);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  display: flex;
  flex-direction: column;
  padding: 30px;
  /* max-height: 400px;  */
  overflow-y: auto;
}

.modalContainer .title {
  display: inline-block;
  text-align: center;
  margin-bottom: 5px;
}

.title h2 {
  font-weight: var(--font-bold);
  margin-top: -10px;
  color: var(--title-color);
  margin-bottom: 1.5rem;
}

.titleCloseBtn {
  display: flex;
  justify-content: flex-end;
}

.titleCloseBtn button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  width: 20px;
  height: 25px;
}

.modalContainer .body {
  flex: 50%;
  text-align: center;
}

.modalContainer .ProfileButton {
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalContainer .ProfileButton button {
  width: 170px;
  height: 30px;
  border: none;
  background-color: cornflowerblue;
  color: var(--body-color);
  border-radius: 8px;
  cursor: pointer;
  margin-bottom: 25px;
  padding: 0;
}

.OpenModalBtn {
  text-align: left;
  background-color: var(--white);
  display: flex;
  align-items: center;
  border-radius: 20px;
  height: 100px;
  width: 80vw;
  margin-bottom: 20px;
  cursor: pointer;
  border: 1px solid var(--light-gray);
  padding-left: 30px;
  font-size: var(--h2-font-size);
  font-weight: var(--font-semibold);
  color: var(--title-color);
  box-shadow: var(--shadow);
  transition: background-color 0.1s ease;
}

.OpenModalBtn:hover {
  background-color: var(--light-gray); /* Change to your desired hover color */
}

.component {
  display: flex;
  font-size: var(--normal-font-size);
  margin-bottom: 30px;
  justify-content: space-between;
  border: 1px solid var(--light-gray);
  border-radius: 20px;
  background-color: var(--white);
  align-items: center;
  padding: 25px;
  box-sizing: border-box;
  white-space: normal;
  box-shadow: var(--shadow);
}

.component h5 {
  font-size: var(--small-font-size);
  width: 15%;
  text-align: left;
  margin-right: 20px;
}

.tags {
  width: 85%;
  text-align: left;
  display: flex;
  flex-wrap: wrap;
  gap: 7px;
}

.component p,
.component a {
  width: 85%;
  /* background-color: pink; */
  text-align: left;
  /* margin-right: 20px */
}

.modal__link {
  text-decoration: underline;
  overflow: hidden;
}

.twoStack {
  display: flex;
  flex-direction: row;
  width: 100%;
  gap: 10px;
}

.twoStack .component {
  width: 50%;
}

.twoStack .component p,
.twoStack .component a {
  width: 70%;
}

.twoStack h5 {
  width: 30%;
}

.modal__text {
  overflow: hidden;
}

#AcademicBackground h5,
#PracticeArea h5 {
  margin-bottom: 4px;
}

@media (max-width: 1000px) {
  .twoStack {
    flex-direction: column; /* This will stack the components vertically */
    gap: 0px;
  }
  .twoStack .component {
    width: 100%;
  }
  .component {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .component p,
  .component h5,
  .component a {
    width: 100%;
    text-align: center;
    /* background-color: pink; */
    margin: 0;
  }

  .tags {
    display: grid; /* Sets Grid Layout */
    justify-items: center;
  }

  .modal__span {
    width: fit-content;
  }
}

