.home {
  max-height: 100vh;
  overflow-y: scroll;
  min-width: fit-content;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.home::-webkit-scrollbar {
  display: none;
}

.home__header {
  position: sticky;
  top: 0;
  z-index: 100;
  background-color: var(--body-color);
  border-bottom: 1px solid var(--light-gray);
  border-width: 100%;
  display: flex;
  justify-content: center;
}

.header__button {
  background-color: var(--body-color);
  padding: 25px;
  padding-top: 30px;
  width: 40%;
}

.header__button:hover {
  background-color: var(--light-gray);
  transition: 0.2s;
}

.home__link {
  color: var(--text-color);
  padding-bottom: 20px;
}

.home__link.active {
  border-bottom: 5px solid var(--first-color);
  color: var(--title-color);
}

.no-follow-message {
  text-align: center;
  font-size: x-large;
  justify-content: center;
  margin-top: 25%;
}


.no-follow-message .sad-face {
  margin-right: 5px;
}

.load__more {
  display: flex;
  justify-content: center;
  margin: 20px auto;
  background-color: #fefefe;
  color: var(--text-color);
}
